<template>
	<div class="page-with-padding">
		<div class="buttons-big">
			<wv-button type="primary"> Normal</wv-button>
			<wv-button type="primary" :disabled="true"> Disabled</wv-button>
			<wv-button type="default"> Normal</wv-button>
			<wv-button type="default" :disabled="true"> Disabled</wv-button>
			<wv-button type="warn"> Normal</wv-button>
			<wv-button type="warn" :disabled="true"> Disabled</wv-button>
		</div>

		<div class="buttons-plain">
			<wv-button type="default" :plain="true"></wv-button>
			<wv-button type="default" :plain="true" :disabled="true"></wv-button>
			<wv-button type="primary" :plain="true"></wv-button>
			<wv-button type="primary" :plain="true" :disabled="true"></wv-button>
		</div>

		<div class="buttons-mini">
			<wv-button type="primary" :mini="true"></wv-button>
			<wv-button type="default" :mini="true"></wv-button>
			<wv-button type="warn" :mini="true"></wv-button>
		</div>
	</div>
</template>

<script type="text/babel">
export default {
  mounted () {
  }
}
</script>

<style scoped lang="scss">
	.buttons-big {
		display: block;
		margin: 10px auto;
	}
	
	.buttons-plain {
		display: block;
		width: 60%;
		margin: 10px auto;
	}
	
	.buttons-mini {
		display: block;
		width: 60%;
		margin: 10px auto;
	}
</style>



